import React from 'react';
import ReactDOM from 'react-dom';
import {render} from 'react-dom';

var i = 0;
var p = 0;
var o = 0;
var txt = "Inspired by the many confessions  made by the wonderfully lost and confused young adults in my life. I raise my pen to you.";
var speed = 80;
 
class App extends React.Component {
  render () {
    return(
        <MainScreen></MainScreen>
        );
  }
}

  function Line1() {
    if (i < txt.length) {
      document.getElementById("line1").innerHTML += txt.charAt(i);
      i++;
      if(i==57){
        document.getElementById("line1").innerHTML += "<br />";
      }
      if(i==100){
        document.getElementById("line1").innerHTML += "<br />";
        document.getElementById("line1").innerHTML += "<br />";
      }
      setTimeout(Line1, speed);
    }
  }

class MainScreen extends React.Component {
  constructor(props) {
  super(props);
  this.state = { screen: '0' };
  }
  componentDidMount() {
    Line1()
  }
  render () {
    return(
        <div style={styles.firstScreen}>
        <div style={styles.skipIntro}>SKIP  INTRO</div>
        <div style={styles.firstText}>
        <div id="line1" style={styles.justifyText}></div>
        </div>
        </div>
        );
  }
}

let styles = {
    firstScreen: {
        height : screen.availHeight-66,
        width: screen.availWidth,
        backgroundColor: '#1d1842',
        display:'flex',
        flex: 1,
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
  },
    firstText:{
        display:'flex',
        flex: 1,
        textAlign: 'center', 
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        color:'white',
        fontSize: 30,
        width : screen.availHeight-500,
  },
  skipIntro:{
        color:'white',
        fontFamily:'Poppins',
        fontWeight:'SemiBold',
        display:'flex',
        fontSize: 15,
        height:20,
        width:20,
  },
}


render(<App/>, document.getElementById('app'));


